.features {
  padding-top: 33px;

  @media (min-width: $tablet) {
    padding-top: 44px;
  }

  @media (min-width: $desktop) {
    padding-top: 55px;
  }
}

.features__container {
  @media (min-width: $tablet) {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 256px 332px 256px 256px;
  }
}

.features__picture {
  max-height: 260px;

  @media (min-width: $tablet) {
    width: 100%;
    max-height: 256px;

    &--town {
      grid-column-start: 2;
      grid-column-end: 4;
    }

    &--devil {
      grid-column-start: 1;
      grid-column-end: 3;
      grid-row-start: 3;
    }
  }
}

.features__img {
  width: 100%;
  max-width: 100%;
  max-height: 260px;
  object-fit: cover;

  @media (min-width: $tablet) {
    height: 256px;
    width: calc(100% * 2);
    margin: 0;
  }
}

.features__ul {
  @media (min-width: $tablet) {
    grid-column-start: 1;
    grid-column-end: 4;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}

.features__title {
  @include fields();
  @include font(18px, 27px, 700);
  margin: 0 auto 30px;
  width: 260px;
  text-align: center;

  @media (min-width: $tablet) {
    @include font(21px, 26px, 700);
    margin-bottom: 31px;
    width: 490px;
  }

  @media (min-width: $desktop) {
    margin-bottom: 32px;
  }
}

.features__text {
  @include fields();
  @include font(14px, 26px, 400);
  margin: 0 auto 35px;
  width: 260px;
  text-align: center;

  @media (min-width: $tablet) {
    margin-bottom: 53px;
    width: 490px;
  }

  @media (min-width: $desktop) {
    margin-bottom: 41px;
  }
}
